@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@400;700&display=swap);
.main {
    display: grid;
    grid-template-areas: 
        "header"
        "container"
        "footer";
        grid-template-columns: 1fr;
        grid-template-rows: 10vh auto auto;
}

/* Header Styles */
header {
    grid-area: header;
    background-color: #5ad1f2;
    border-bottom: 3px solid #f0d43a;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

header div {
    width: 40%;
}

header h2 a {
    font-size: 1.2em;
}

#navDiv {
    display: flex;
    justify-content: center;
    align-items: center;
}

nav ul {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

nav ul li {
    list-style: none;
    padding: 0 20px;
}

nav ul li a {
    font-weight: bold;
}

nav ul li a:hover {
    color: #5e75a7;
}

/* Body Styles */
.container {
    grid-area: container;
    background-image: url("/img/background.png");
    background-size: cover;
    background-repeat: no-repeat;
    min-height: 70vh;
  }

  .container-grid {
    display: grid;
    grid-template-areas: 
        "left-col right-col";
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr;
}

.left-col {
    grid-area: left-col;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.right-col {
    grid-area: right-col;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

  /* Footer Styles */
  footer {
      grid-area: footer;
      background-color: #5ad1f2;
      border-top: 3px solid #f0d43a;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 3em 0;
  }

  .footerDiv {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
  }

  .socialUL {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: 2em;
  }

  .socialUL li {
      list-style: none;
      margin: 0 15px;
  }

  .socialUL li a {
      text-decoration: none;
  }

  .socialUL li a i {
      font-size: 1.75em;
  }

  .socialUL li a i:hover {
      color: #5e75a7;
  }

  .footerDiv p {
      font-size: 0.9em;
      margin-top: 20px;
  }

  /* MAX SCREEN WIDTH 786px */
@media only screen and (max-width: 768px) {
    .hide {
        display: none;
    }

    nav ul li {
        text-align: center;
    }

    .container-grid {
        grid-template-areas: 
            "right-col"
            "left-col"; 
            grid-template-columns: 1fr;
            grid-template-rows: 1fr auto;
    }

    .left-col {
        justify-content: center;
        margin-bottom: 2em;
    }

    .footerDiv {
        padding: 0 1em;
        text-align: center;
    }
  }

  /* MAX SCREEN WIDTH 1065px */
  @media only screen and (max-width: 1065px) {
    header div {
        width: 45%;
    }

    nav ul li {
        padding: 0 10px;
    }
  }
.tag-block {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 80%;
}

.tag-block h1 {
    font-size: 2.5em;
    line-height: 1.5em;
    text-align: center;
}

/* Home & About Image Styles */
.img-block {
    max-width: 100%;
}

.img-home {
    border-radius: 10px;
    max-width: 450px;
    width: 100%;
}

 /* MAX SCREEN WIDTH 786px */
 @media only screen and (max-width: 768px) {
    .img-block {
        margin-top: 2em;
        max-width: 80%;
        padding-left: 2em;
    }
  }
.about-block {
    width: 80%;
}

.about-block h2 {
    margin-bottom: 1em;
}

.about-block p {
    line-height: 1.5;
    margin-bottom: 1em;
}

.img-about {
    border-radius: 10px;
    max-width: 450px;
    width: 100%;
}

 /* MAX SCREEN WIDTH 786px */
 @media only screen and (max-width: 768px) {
    .about-block {
        margin-top: 2em;
    }
  }

  /* MAX SCREEN WIDTH 900px */
 @media only screen and (max-width: 900px) {
    .about-block {
        margin-top: 2em;
    }

    .about-img-block {
        margin: 0 auto;
        width: 80%;
    }
  }
.work {
    grid-column: 1/ span 2;
    display: flex;
    justify-content: center;
    align-items: center;
}

.project-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 90%;
}

.project-container h2 {
    margin-top: 1.5em;
}

.project-block {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.project-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #e0e0e0;
    background-color: #053745;
    border: 2px solid #f0d43a;
    border-radius: 10px;
    margin: 1.5em 0.75em;
    min-width: 350px;
    max-width: 20%;
    padding: 1em;
}

.project-img {
    height: 200px;
}

.project-card img {
    margin-bottom: 20px;
    max-height: 180px;
    max-width: 315px;
    width: 100%;
}

.project-content {
    height: 170px;
    text-align: center;
    width: 90%;
}

.project-card h3 {
    margin-bottom: 1em;
}

.project-card p {
    margin-bottom: 2em;
}

.card-btn {
    background-color: #e0e0e0;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1em;
    margin: 10px;
    padding: 0.7em 1.5em;
}
.contact-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 70vh;
}

.contact-content h1 {
    font-size: 2.5em;
    margin-bottom: 1.5em;
}

.contact-content h2 {
    margin-bottom: 1em;
}

.socialContact {
    margin: 0 0 2em 0;
}

.socialContact li a i {
    font-size: 2.5em;
}

.contact-content h3 {
    margin-bottom: 1em;
    text-align: center;
}

.contact-content button {
    margin-top: 0.5em;
}

 /* MAX SCREEN WIDTH 786px */
 @media only screen and (max-width: 768px) {
    .contact-content {
        padding: 0 2em;
    }
  }
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Comfortaa', cursive;
  font-size: 16px;
  color: #3b4a6b;
}

a {
  color: #3b4a6b;
  text-decoration: none;
}

.button {
  background-color: #f0d43a;
    border: 1px solid #bba421;
    border-radius: 10px;
    color: #3b4a6b;
    cursor: pointer;
    font-size: 1.2em;
    margin-top: 1.5em;
    padding: 0.5em 1.25em;
}
  
.button:hover {
    background-color: #e4d05b;
    color: #1a3468;
}

 /* MAX SCREEN WIDTH 786px */
@media only screen and (max-width: 768px) {
  body {
    font-size: 14px;
  }
}
